<template>
  <div>
    <main id="main">
      <section id="specials" class="specials">
        <div class="container" data-aos="fade-up">
          <div class="section-title" style="margin-top: 150px;">
            <h2>Deals</h2>
            <p>아파트/주택 실거래가 검색</p>
            <div id="map">
              <GmapMap
                ref="mapRef"
                :center="center"
                :zoom="16"
                style="margin-top: 40px; margin-bottom: 20px; height: 350px"
              ></GmapMap>
            </div>
            <div style="text-align:center;">
              <select id="sido" @change="getGugun()" v-model="selectedSido">
                <option disabled value="도/광역시">도/광역시</option>
                <option v-for="(op, i) in sido" :value="op" :key="i">
                  {{ op.sidoname }}
                </option>
              </select>
              <select id="gugun" @change="getDong()" v-model="selectedGugun">
                <option disabled value="구/군">구/군</option>
                <option v-for="(op, i) in gugun" :value="op" :key="i">
                  {{ op.gugunname }}
                </option>
              </select>
              <select id="dong" v-model="selectedDong">
                <option disabled value="읍/면/동">읍/면/동</option>
                <option v-for="(op, i) in dong" :value="op.dongname" :key="i">
                  {{ op.dongname }}
                </option>
              </select>
            </div>

            <div style="text-align:center; margin-top: 24px" class="check">
              <input type="checkbox" v-model="apt" id="capt" /><label for="capt"
                >아파트</label
              >
              <input type="checkbox" v-model="house" id="chouse" /><label
                for="chouse"
                >연립다세대</label
              >
              <input type="checkbox" v-model="deal" id="cdeal" /><label
                for="cdeal"
                >매매</label
              >
              <input type="checkbox" v-model="rent" id="crent" /><label
                for="crent"
                >전월세</label
              >
            </div>
            <div style="text-align:center; margin-top: 10px">
              <input type="month" v-model="month" />
              <button @click="getApt()" class="black-button">검색</button>
              <button @click="addInterest()" class="black-button">
                관심지역 추가
              </button>
            </div>
          </div>
          <div v-if="searched">
            <deal-list
              :list-array="deals"
              :page-zero="pageZero"
              @next-page="nextPage"
              @prev-page="prevPage"
              @page-initialized="pageInitialized"
            />
          </div>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import Swal from 'sweetalert2';
import shuffle from 'lodash/shuffle';
import take from 'lodash/take';
import initCustomMarker from '@/assets/js/custom-marker';
import { gmapApi as google } from 'vue2-google-maps';
import DealList from '@/components/DealList.vue';
import http from '@/util/http-common';
let CustomMarker;
const API_KEY = process.env.VUE_APP_GOOGLE_MAP_API_KEY;
const API_URL = 'https://maps.googleapis.com/maps/api/geocode/json';
export default {
  name: 'Map',
  components: {
    DealList,
  },
  data() {
    return {
      sido: [],
      gugun: [],
      dong: [],
      deals: [],
      selectedSido: '도/광역시',
      selectedGugun: '구/군',
      selectedDong: '읍/면/동',
      apt: 'true',
      house: 'true',
      deal: 'true',
      rent: 'true',
      month: '2020-11',
      pageNum: 0,
      pageSize: 5,
      pageZero: false,
      center: {
        lat: 37.543339075307266,
        lng: 126.95150089628109,
      },
      markers: [],
      CustomMarker: null,
      positions: [],
      searched: false,
    };
  },
  created() {
    http.get('/map/sido').then(({ data }) => {
      this.sido = data;
    });
  },
  computed: {
    google,
    monthComputed: function() {
      return this.month.replace('-', '');
    },
  },
  watch: {
    google(val) {
      CustomMarker = initCustomMarker(val);
    },
  },
  methods: {
    getGugun() {
      this.selectedGugun = '구/군';
      this.selectedDong = '읍/면/동';
      http
        .get('/map/gugun?sido=' + this.selectedSido.sidocode)
        .then(({ data }) => {
          this.gugun = data;
        });
    },
    getDong() {
      this.selectedDong = '읍/면/동';
      http
        .get('/map/dong?gugun=' + this.selectedGugun.guguncode)
        .then(({ data }) => {
          this.dong = data;
        });
    },
    getApt() {
      this.pageZero = true;
      if (this.selectedGugun.guguncode == null) {
        Swal.fire({
          icon: 'error',
          title: 'Oops...',
          text: '구/군을 선택해주세요.',
        });
        return;
      }
      http
        .get(
          `/map/apt?dong=${this.selectedDong}&gugun=${this.selectedGugun.guguncode}&apt=${this.apt}&house=${this.house}&deal=${this.deal}&rent=${this.rent}&month=${this.monthComputed}`
        )
        .then(({ data }) => {
          this.deals = data;
          this.geocode();
        })
        .catch((e) => {
          console.log('getApt() : 에러가 발생했습니다.' + e);
        });
    },
    geocode() {
      this.searched = true;
      const start = this.pageNum * this.pageSize,
        end = start + this.pageSize,
        data = this.deals.slice(start, end);
      this.clearMarkers();
      this.positions = [];
      let self = this;
      data.forEach(function(e) {
        const params = {
          key: API_KEY,
          address: `${e.dong}+${e.aptName}+${e.jibun}`,
        };
        http
          .get(API_URL, { params })
          .then((ret) => {
            if (ret.status == 200) {
              if (ret.data.status == 'OK') {
                let pos = ret.data.results[0].geometry.location;
                pos.name = e.aptName;
                self.positions.push(pos);
              }
            }
          })
          .catch((e) => {
            console.log('Geocode 에러가 발생했습니다. ' + e);
          });
      });
      setTimeout(() => {
        this.addMarkers();
        this.center.lat = this.positions[0].lat;
        this.center.lng = this.positions[0].lng;
      }, 800);
    },
    clearMarkers() {
      this.markers.map((marker) => {
        marker.onRemove();
      });
      this.markers = [];
    },
    addMarkers() {
      take(shuffle(this.positions), 20).map(({ name, lat, lng }, i) => {
        const el = document.createElement('div');
        el.textContent = name;
        el.setAttribute('data-marker-index', i);
        const t = new CustomMarker(new this.google.maps.LatLng(lat, lng), el);
        this.$refs['mapRef'].$mapPromise.then((map) => {
          t.setMap(map);
        });
        this.markers.push(t);
      });
    },
    nextPage() {
      ++this.pageNum;
      this.geocode();
    },
    prevPage() {
      --this.pageNum;
      this.geocode();
    },
    pageInitialized() {
      this.pageNum = 0;
      this.pageZero = false;
    },
    addInterest() {
      if (
        this.selectedGugun.gugunname == null ||
        this.selectedGugun.gugunname == '구/군'
      ) {
        Swal.fire({
          icon: 'error',
          title: '등록 실패',
          text: '등록할 지역을 선택해주세요.',
        });
        return;
      }
      if (this.$store.state.userId == '') {
        Swal.fire({
          icon: 'error',
          title: '등록 실패',
          text: '관심지역 등록을 위해서는 로그인이 필요합니다.',
        });

        return;
      }
      http
        .post('/interest', {
          sido: this.selectedSido.sidoname,
          gugun: this.selectedGugun.gugunname,
          dong: this.selectedDong,
          code: this.selectedGugun.guguncode,
          userId: this.$store.state.userId,
        })
        .then((response) => {
          if (response.data == 1) {
            Swal.fire({
              icon: 'success',
              title: '등록 완료',
              text: '관심 지역이 정상적으로 등록되었습니다.',
            });
          } else {
            Swal.fire({
              icon: 'error',
              title: '등록 실패',
              text: '이미 등록된 지역입니다.',
            });
          }
        })
        .catch((e) => {
          console.log('addInterest 에러가 발생했습니다. ' + e);
        });
    },
  },
};
</script>

<style lang="scss">
.custom-marker {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -100%);
  background-color: white;
  padding: 0 8px;
  border-radius: 28px;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 0px 0px 1px,
    rgba(0, 0, 0, 0.18) 0px 1px 2px;
  color: #222;
  overflow-y: auto;
  height: 28px;
  line-height: 28px;
  font-weight: bold;
  cursor: pointer;
  transition: transform 0.15s ease-in-out;
  font-size: 9px;
}
.custom-marker:hover {
  transform: translate(-50%, -100%) scale(1.2);
}
.custom-marker-anchor {
  position: absolute;
  width: 100%;
  bottom: 8px;
  left: 0;
}
.custom-marker-container {
  cursor: auto;
  height: 0;
  position: absolute;
  width: 200px;
  &.active {
    .custom-marker {
      z-index: 1000;
      background-color: #f7530e;
      color: #fff;
      transform: translate(-50%, -100%) scale(1.1);
    }
  }
}
input[type='month'] {
  border-radius: 5px;
  font-family: 'Nanum Gothic', sans-serif;
  text-align: right;
  background-color: black;
  width: 150px;
  color: white;
  margin-right: 20px;
}
input[type='checkbox'] {
  background-color: white;
  position: relative;
  width: 1em;
  height: 1em;
  color: white;
  border-radius: 4px;
  appearance: none;
  outline: 0;
  cursor: pointer;
  transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
  &::before {
    position: absolute;
    content: '';
    display: block;
    top: 0px;
    left: 3px;
    width: 8px;
    height: 13px;
    border-style: solid;
    border-color: white;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
    opacity: 0;
  }
  &:checked {
    color: white;
    border-color: #cda45e;
    background: #cda45e;
    &::before {
      opacity: 1;
    }
    ~ label::before {
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }
  }
}

label {
  position: relative;
  cursor: pointer;
  font-size: 1.2em;
  font-family: 'Nanum Gothic', sans-serif;
  padding: 0 0.25em 0;
  user-select: none;
  &::before {
    position: absolute;
    content: attr(data-content);
    color: lightgray;
    clip-path: polygon(0 0, 0 0, 0% 100%, 0 100%);
    transition: clip-path 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
}
select {
  background-color: black;
  width: 150px;
  color: white;
  padding: 4px;
  border-radius: 5px;
}
option {
  font-family: 'Nanum Gothic', sans-serif;
}
</style>
<style lang="css" scoped>
.black-button {
  font-family: 'Nanum Gothic', sans-serif;
  border: 2px solid #cda45e;
  color: #fff;
  border-radius: 50px;
  padding: 5px 18px;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 0.3s;
  background-color: black;
  margin-right: 15px;
}
</style>
