<template>
  <div class="container pt-3">
    <div style="margin-top:150px; margin-bottom:100px;text-align : center;">
      <div class="login-form">
        <div class="main-div col-sm-6" style="background-color: #0C0B09">
          <div class="panel">
            <h1 style="color: #cda45e">Pent House</h1>
            <p>아이디와 비밀번호를 입력해주세요.</p>
          </div>
          <!-- <form id="login" @submit.prevent="login"> -->
          <div class="form-group">
            <input
              type="text"
              class="form-control"
              id="loginID"
              name="id"
              v-model="user.id"
              placeholder="ID"
            />
          </div>

          <div class="form-group">
            <input
              type="password"
              class="form-control"
              id="loginPassword"
              name="password"
              v-model="user.password"
              placeholder="Password"
              @keypress.enter="login"
            />
          </div>
          <div class="forgot">
            <a href="#">비밀번호 찾기</a>
          </div>
          <input
            @click="login"
            id="loginBtn"
            class="btn btn-primary"
            value="로그인"
            dismiss-data="modal"
          />
          <!-- </form> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Swal from 'sweetalert2';

export default {
  data() {
    return {
      user: {
        id: '',
        password: '',
      },
    };
  },

  computed: {
    nextRoute() {
      return this.$route.params.nextRoute ? this.$route.params.nextRoute : '';
    },
  },
  methods: {
    login() {
      // LOGIN 액션 실행
      // 서버와 통신(axios)을 해 토큰값을 얻어야 하므로 Actions를 호출.
      console.log(this.user.id);
      this.$store
        .dispatch('LOGIN', this.user)
        .then((response) => {
          //this.$router.push(`/${this.nextRoute}`);
          if (response != 'fail') {
            this.$router.replace(`/${this.nextRoute}`);
          }
        })
        .catch(({ message }) => {
          alert(message);
        });

      // $('#loginBtn').attr('data-dismiss', 'modal');
    },
  },
};
</script>
