<template>
  <div>
    <table class="table">
      <colgroup>
        <col :style="{ width: '10%' }" />
        <col :style="{ width: '50%' }" />
        <col :style="{ width: '10%' }" />
        <col :style="{ width: '25%' }" />
      </colgroup>
      <tr>
        <th>번호</th>
        <th>제목</th>
        <th>작성자</th>
        <th>날짜</th>
      </tr>
      <tr
        v-for="(item, index) in paginatedData"
        :key="index"
        :no="item.no"
        :title="item.title"
        :writer="item.writer"
        :regtime="item.regtime"
      >
        <td>{{ item.no }}</td>
        <td>
          <a @click="detail(item.no)">{{ item.title }}</a>
        </td>
        <td>{{ item.writer }}</td>
        <td>{{ getFormatDate(item.regtime) }}</td>
      </tr>
    </table>

    <div class="pageNav">
      <button :disabled="pageNum === 0" @click="prevPage">
        이전</button
      >&nbsp; <span>{{ pageNum + 1 }} / {{ pageCount }} 페이지</span>&nbsp;
      <button :disabled="pageNum >= pageCount - 1" @click="nextPage">
        다음
      </button>
    </div>
  </div>
</template>
<script>
import moment from 'moment';
export default {
  name: 'NoticeList',
  data() {
    return {
      pageNum: 0,
    };
  },
  props: {
    listArray: {
      type: Array,
      required: true,
    },
    pageSize: {
      type: Number,
      required: false,
      default: 5,
    },
    pageZero: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  watch: {
    pageZero() {
      if (this.pageZero === true) {
        this.pageNum = 0;
        this.$emit('page-initialized');
      }
    },
  },
  methods: {
    nextPage() {
      this.pageNum++;
      this.$emit('next-page');
    },
    prevPage() {
      this.pageNum--;
      this.$emit('prev-page');
    },
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD');
    },
    detail(no) {
      this.$emit('show-detail', no);
    },
  },
  computed: {
    pageCount() {
      let listLeng = this.listArray.length,
        listSize = this.pageSize,
        page = Math.floor(listLeng / listSize);
      if (listLeng % listSize > 0) page += 1;
      if (page == 0) page = 1;
      return page;
    },
    paginatedData() {
      const start = this.pageNum * this.pageSize,
        end = start + this.pageSize;
      return this.listArray.slice(start, end);
    },
  },
};
</script>
<style lang="css" scoped>
.pageNav {
  color: white;
  text-align: center;
  margin-top: 30px;
}
table {
  width: 100%;
}
th {
  color: white;
}
td {
  padding: 6px;
  color: white;
}
td > a {
  color: #cda45e;
}
.pageNav > button {
  font-family: 'Nanum Gothic', sans-serif;
  color: white;
  border-radius: 50px;
  padding: 2.8px 14px;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 0.3s;
  background-color: #cda45e;
}
.pageNav > button:disabled {
  background-color: gray;
}
.black-button {
  font-family: 'Nanum Gothic', sans-serif;
  border: 2px solid #cda45e;
  border-radius: 10px;
  height: 40px;
  color: #fff;
  padding: 5px 18px;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 0.3s;
  background-color: #0c0b09;
  margin-right: 15px;
}
</style>
