<template>
  <div>
    <!-- ======= 회원 정보 확인 ======= -->
    <section id="book-a-table" class="book-a-table">
      <div class="container" data-aos="fade-up" style="margin-top: 150px;">
        <div class="section-title">
          <h2>Membership</h2>
          <p>회 원 정 보</p>
        </div>

        <form
          action="forms/book-a-table.php"
          id="mform"
          method="post"
          role="form"
          class="php-email-form"
          data-aos="fade-up"
          data-aos-delay="100"
        >
          <div class="form-row">
            <div class="col-lg-4 col-md-6 form-group">
              <input
                type="text"
                name="name"
                class="form-control"
                readonly="readonly"
                :value="user.name"
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-4 col-md-6 form-group">
              <input
                type="email"
                class="form-control"
                readonly="readonly"
                :value="user.email"
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-4 col-md-6 form-group">
              <input
                type="text"
                class="form-control"
                readonly="readonly"
                :value="user.phone"
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-6 col-md-6 form-group">
              <input
                type="text"
                class="form-control"
                readonly="readonly"
                :value="user.id"
                style="color: #F2CB61"
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-6 col-md-6 form-group">
              <input
                type="password"
                class="form-control"
                readonly="readonly"
                :value="user.password"
                style="color: #F2CB61"
              />
              <div class="validate"></div>
            </div>
          </div>
          <div class="form-group">
            <textarea
              class="form-control"
              name="message"
              rows="2"
              readonly="readonly"
              v-model="user.address"
            ></textarea>
            <div class="validate"></div>
          </div>
          <div class="text-center">
            <button type="button" style="margin-right: 20px;" @click="withdraw">
              회 원 탈 퇴
            </button>
            <button type="button" style="margin-right: 20px;" @click="modify">
              회 원 수 정
            </button>
          </div>
        </form>
      </div>
    </section>
  </div>
</template>
<!-- <h2>내정보보기</h2>
    이름 : {{ user.username }}<br />
    이메일 : {{ user.email }}<br />
    폰번호 : {{user.phone}}
    아이디 : {{ user.userid }}<br />
    비밀번호 : {{user.password}}
    주소 : {{ user.address }}<br /> 
  </div> -->
<script>
import axios from 'axios';
import Swal from 'sweetalert2';

const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  data() {
    return {
      user: '',
    };
  },
  created() {
    //회원정보 바로 가져오기.
    axios
      .get(`${SERVER_URL}/user/info`)
      .then((response) => {
        // console.log(response.data.user);
        this.user = response.data.user;
        console.log(this.user);
      })
      .catch(() => {
        this.$store.dispatch('LOGOUT').then(() => this.$router.replace('/'));
      });
  },
  methods: {
    withdraw() {
      Swal.fire({
        title: '회원 탈퇴',
        text: 'PentHouse 서비스를 이용하실 수 없습니다. 탈퇴하시겠습니까?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#FFBB00',
        cancelButtonColor: '#5D5D5D',
        confirmButtonText: '네, 탈퇴하겠습니다.',
        cancelButtonText: '취소',
      }).then((result) => {
        if (result.isConfirmed) {
          axios
            .delete(`${SERVER_URL}/user/${this.user.id}`)
            .then(({ data }) => {
              this.$store.dispatch('LOGOUT'); //토큰 지워줌
              if (data === 'success') {
                Swal.fire({
                  title: '탈퇴 완료',
                  text: '다음에 또 만나요.',
                  icon: 'success',
                  confirmButtonText: '닫기',
                  confirmButtonColor: '#5D5D5D',
                });
              }
              this.$router.replace('/');
            })
            .catch(() => {
              alert('탈퇴시 에러가 발생했습니다.');
            });
        }
      });
    },
    modify() {
      this.$router.push('/memberupdate');
    },
  },
};
</script>
