<template>
  <div>
    <main id="main">
      <section id="specials" class="specials">
        <div class="container" data-aos="fade-up">
          <div class="section-title" style="margin-top: 150px;">
            <h2>Interest</h2>
            <p>관심 지역</p>
            <div style="margin-top:30px;">
              <button
                class="bubble"
                v-for="(area, i) in interest"
                :value="area.code"
                :key="i"
                @click="show(area)"
                @contextmenu.prevent="remove(area)"
              >
                #{{ area.sido }} {{ area.gugun }}
                {{ area.dong == '읍/면/동' ? '' : area.dong }}
              </button>
            </div>
            <div v-if="area != ''" style="margin-top:50px;">
              <h4>
                {{ area.sido }} {{ area.gugun }}
                {{ area.dong == '읍/면/동' ? '' : area.dong }}
              </h4>
              <div id="map">
                <GmapMap
                  ref="mapRef"
                  :center="center"
                  :zoom="16"
                  style="margin-top: 40px; margin-bottom: 20px; height: 350px"
                ></GmapMap>
              </div>
              <div style="text-align:center; margin-top: 24px" class="check">
                <input type="checkbox" v-model="apt" id="capt" /><label
                  for="capt"
                  >아파트</label
                >
                <input type="checkbox" v-model="house" id="chouse" /><label
                  for="chouse"
                  >연립다세대</label
                >
                <input type="checkbox" v-model="deal" id="cdeal" /><label
                  for="cdeal"
                  >매매</label
                >
                <input type="checkbox" v-model="rent" id="crent" /><label
                  for="crent"
                  >전월세</label
                >
              </div>
              <div
                style="text-align:center; margin-top: 10px;margin-bottom: 20px"
              >
                <input type="month" v-model="month" />
                <button @click="show(area)" class="black-button">검색</button>
              </div>
              <deal-list
                :list-array="deals"
                :page-zero="pageZero"
                @next-page="nextPage"
                @prev-page="prevPage"
                @page-initialized="pageInitialized"
              />
            </div>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>
<script>
import Swal from 'sweetalert2';
import http from '@/util/http-common';
import shuffle from 'lodash/shuffle';
import take from 'lodash/take';
import initCustomMarker from '@/assets/js/custom-marker';
import { gmapApi as google } from 'vue2-google-maps';
import DealList from '@/components/DealList.vue';
let CustomMarker;
const API_KEY = process.env.VUE_APP_GOOGLE_MAP_API_KEY;
const API_URL = 'https://maps.googleapis.com/maps/api/geocode/json';
export default {
  name: 'Interest',
  components: {
    DealList,
  },
  data() {
    return {
      interest: [],
      area: '',
      apt: 'true',
      house: 'true',
      deal: 'true',
      rent: 'true',
      month: '2020-11',
      pageNum: 0,
      pageSize: 5,
      pageZero: false,
      center: {
        lat: 37.543339075307266,
        lng: 126.95150089628109,
      },
      markers: [],
      CustomMarker: null,
      positions: [],
      deals: [],
    };
  },
  created() {
    if (this.$store.state.userId == '') {
      Swal.fire({
        icon: 'error',
        title: '로그인 필요',
        text: '관심 지역 둘러보기를 위해 로그인이 필요합니다.',
      });
      this.$router.push('/login');
      return;
    }
    this.getInterest();
  },
  computed: {
    google,
    monthComputed: function() {
      return this.month.replace('-', '');
    },
  },
  watch: {
    google(val) {
      CustomMarker = initCustomMarker(val);
    },
  },
  methods: {
    getInterest() {
      http
        .get(`/interest?userId=${this.$store.state.userId}`)
        .then((response) => {
          this.interest = response.data;
        })
        .catch((e) => {
          console.log('getInterest 에러가 발생했습니다. ' + e);
        });
    },
    show(area) {
      this.area = area;
      http
        .get(
          `/map/apt?dong=${area.dong}&gugun=${area.code}&apt=${this.apt}&house=${this.house}&deal=${this.deal}&rent=${this.rent}&month=${this.monthComputed}`
        )
        .then(({ data }) => {
          this.deals = data;
          this.geocode();
        })
        .catch((e) => {
          console.log('show() : axios에러가 발생했습니다.' + e);
        });
    },
    geocode() {
      const start = this.pageNum * this.pageSize,
        end = start + this.pageSize,
        data = this.deals.slice(start, end);
      this.clearMarkers();
      this.positions = [];
      let self = this;
      data.forEach(function(e) {
        const params = {
          key: API_KEY,
          address: `${e.dong}+${e.aptName}+${e.jibun}`,
        };
        http
          .get(API_URL, { params })
          .then((ret) => {
            if (ret.status == 200) {
              if (ret.data.status == 'OK') {
                let pos = ret.data.results[0].geometry.location;
                pos.name = e.aptName;
                self.positions.push(pos);
              }
            }
          })
          .catch((e) => {
            console.log('Geocode 에러가 발생했습니다. ' + e);
          });
      });
      setTimeout(() => {
        this.addMarkers();
        this.center.lat = this.positions[0].lat;
        this.center.lng = this.positions[0].lng;
      }, 800);
    },
    clearMarkers() {
      this.markers.map((marker) => {
        marker.onRemove();
      });
      this.markers = [];
    },
    addMarkers() {
      take(shuffle(this.positions), 20).map(({ name, lat, lng }, i) => {
        const el = document.createElement('div');
        el.textContent = name;
        el.setAttribute('data-marker-index', i);
        const t = new CustomMarker(new this.google.maps.LatLng(lat, lng), el);
        this.$refs['mapRef'].$mapPromise.then((map) => {
          t.setMap(map);
        });
        this.markers.push(t);
      });
    },
    nextPage() {
      ++this.pageNum;
      this.geocode();
    },
    prevPage() {
      --this.pageNum;
      this.geocode();
    },
    pageInitialized() {
      this.pageNum = 0;
      this.pageZero = false;
    },
    remove(area) {
      console.log(area);
      console.log('우클릭');
      Swal.fire({
        title: '삭제하시겠습니까?',
        showCancelButton: true,
        confirmButtonText: `삭제`,
      }).then((result) => {
        if (result.isConfirmed) {
          http
            .delete(
              `/interest?code=${area.code}&userId=${this.$store.state.userId}`
            )
            .then(() => {
              this.getInterest();
              Swal.fire('삭제 완료', '정상적으로 삭제되었습니다.', 'success');
            });
        }
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.bubble {
  height: 60px;
  border-color: #cda45e;
  background-color: #0c0b09;
  color: white;
  font-family: 'Nanum Gothic', sans-serif;
  padding-right: 15px;
  padding-left: 15px;
  vertical-align: middle;
  margin: 2px;
}
h4 {
  color: #cda45e;
  font-family: 'Playfair Display', serif;
  font-weight: bold;
}
.black-button {
  font-family: 'Nanum Gothic', sans-serif;
  border: 2px solid #cda45e;
  color: #fff;
  border-radius: 50px;
  padding: 5px 18px;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 0.3s;
  background-color: black;
  margin-right: 15px;
}
</style>
