<template>
  <section id="book-a-table" class="book-a-table">
    <div class="container" data-aos="fade-up" style="margin-top: 150px;">
      <div class="section-title">
        <h2>NEWS</h2>
        <p>오늘의 뉴스</p>
      </div>
      <hr />
      <div class="row text-center">
        <div class="col-lg-4 mb-5" v-for="(item, index) in list" :key="index">
          <h5 class="mb-2">
            <strong
              ><a :href="item.url">{{ item.title }}</a></strong
            >
            <div style="margin-top: 20px;">
              <img
                class="img-fluid rounded-lg"
                :src="item.image_url"
                :alt="item.image_alt"
              />
            </div>
            <hr />
            <p>{{ item.summary }}</p>
          </h5>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
const axios = require('axios');
const cheerio = require('cheerio');

export default {
  data() {
    return {
      list: [],
    };
  },

  created() {
    axios
      .get('https://realestate.joins.com/article/')
      .then((html) => {
        let ulList = [];
        const $ = cheerio.load(html.data);
        const $bodyList = $('div.list_basic ul').children('li');

        $bodyList.each(function(i) {
          ulList[i] = {
            title: $(this)
              .find('strong a')
              .text(),
            url:
              'https://realestate.joins.com/' +
              $(this)
                .find('strong a')
                .attr('href'),
            image_url: $(this)
              .find('span.thumb a img')
              .attr('src'),
            image_alt: $(this)
              .find('span.thumb a img')
              .attr('alt'),
            summary: $(this)
              .find('span.lead a')
              .text()
              .substring(0, 100),
            // date: $(this)
            //   .find('span.txt-time')
            //   .text(),
          };
        });
        let data = ulList.filter((n) => n.title);
        // return data;
        this.list = data;
        // for (let i in data) this.list.push(data[i]);
        console.log('list', data);
      })
      .catch((error) => {
        console.error(error);
      });
  },

  mounted() {
    console.log('mounted');
    console.log(this.list);
    // this.list.push({ dd: 'dd' });
  },
  methods: {},
};
</script>
