<template>
  <div>
    <section id="book-a-table" class="book-a-table">
      <div class="container" data-aos="fade-up">
        <form class="text-left php-email-form">
          <div class="form-row">
            <div class="col-lg-12 col-md-12 form-group">
              <label for="content">리뷰 수정</label
              ><textarea
                type="text"
                class="form-control"
                id="contnet"
                ref="content"
                rows="9"
                v-model="content"
              ></textarea>
            </div>
          </div>
          <div class="text-right">
            <button class="submit-btn" @click.prevent="update">
              수정
            </button>
            <button
              class="submit-btn"
              @click.prevent="list"
              style="margin-left:10px"
            >
              목록
            </button>
          </div>
        </form>
      </div>
    </section>
  </div>
</template>

<script>
import http from '@/util/http-common';
export default {
  name: 'ReviewUpdate',
  data() {
    return {
      title: '',
      content: '',
    };
  },
  props: {
    item: {
      type: Object,
      required: true,
    },
    no: {
      type: Number,
      required: true,
    },
  },
  created() {
    this.title = this.item.title;
    this.content = this.item.content;
  },
  methods: {
    list() {
      this.$emit('show-list');
    },
    update() {
      http
        .put(`/review/${this.no}`, {
          no: this.no,
          writer: this.$store.state.userId,
          regtime: '',
          title: this.title,
          content: this.content,
        })
        .then(() => {
          this.$emit('update-list');
        });
    },
  },
};
</script>
<style lang="css" scoped>
label {
  color: #cda45e;
  font-family: 'Playfair Display', serif;
  font-weight: 700;
}
</style>
<style lang="css">
.submit-btn {
  background: #cda45e;
  border: 0;
  padding: 10px 35px;
  color: #fff;
  transition: 0.4s;
  border-radius: 50px;
}
</style>
