<template>
  <div>
    <main id="main">
      <section id="specials" class="specials">
        <div class="container" data-aos="fade-up">
          <div class="section-title" style="margin-top: 120px;">
            <h2>Notice</h2>
            <p>공지사항</p>
            <div class="text-center form-group">
              <br />

              <div v-if="viewList">
                <div style="text-align:right">
                  <select
                    class="form-control"
                    v-model="selected"
                    style="width:100px;display:inline;margin-right:10px;"
                  >
                    <option disabled value="">선택</option>
                    <option>제목</option>
                    <option>작성자</option> </select
                  >&nbsp;&nbsp;
                  <input
                    type="text"
                    class="form-control"
                    style="width:200px;display:inline;margin-right:10px"
                    v-model="keyword"
                    @keyup.enter="search"
                  />
                  <button class="black-button" @click="search">
                    검색
                  </button>
                </div>
                <br />
                <notice-list
                  :list-array="items"
                  @next-page="nextPage"
                  @prev-page="prevPage"
                  @show-detail="showDetail"
                />
                <div class="text-right" v-if="admin">
                  <button class="black-button" @click="regist">
                    작성
                  </button>
                </div>
              </div>
              <div v-if="viewDetail">
                <notice-detail
                  :no="no"
                  :item="item"
                  @show-list="showList"
                  @update-list="updateList"
                  @update-form="updateForm"
                />
              </div>
              <div v-if="viewRegist">
                <notice-regist
                  @show-list="showList"
                  @update-list="updateList"
                />
              </div>
              <div v-if="viewUpdate">
                <notice-update
                  :no="no"
                  :item="item"
                  @show-list="showList"
                  @update-list="updateList"
                />
              </div>
            </div>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import NoticeList from '@/components/NoticeList.vue';
import NoticeDetail from '@/components/NoticeDetail.vue';
import NoticeRegist from '@/components/NoticeRegist.vue';
import NoticeUpdate from '@/components/NoticeUpdate.vue';
import http from '@/util/http-common';
export default {
  name: 'Notice',
  components: {
    NoticeList,
    NoticeDetail,
    NoticeRegist,
    NoticeUpdate,
  },
  data() {
    return {
      pageNum: 0,
      pageSize: 5,
      items: [],
      original: [],
      keyword: '',
      selected: '',
      item: '',
      no: '',
      viewList: true,
      viewDetail: false,
      viewRegist: false,
      viewUpdate: false,
      admin: false,
    };
  },
  created() {
    http.get('/notice').then(({ data }) => {
      this.items = data;
      this.original = data;
    });
  },
  methods: {
    nextPage() {
      ++this.pageNum;
    },
    prevPage() {
      --this.pageNum;
    },
    search() {
      let result = [];
      if (this.selected == '제목') {
        this.original.forEach((item) => {
          if (item.title.includes(this.keyword)) result.push(item);
        });
      } else {
        this.original.forEach((item) => {
          if (item.writer.includes(this.keyword)) result.push(item);
        });
      }
      this.items = result;
    },
    showDetail(no) {
      this.no = no;
      http.get(`/notice/${this.no}`).then(({ data }) => {
        this.item = data;
        console.dir(data);
        this.viewList = false;
        this.viewDetail = true;
      });
    },
    showList() {
      this.viewList = true;
      this.viewDetail = false;
      this.viewUpdate = false;
      this.viewRegist = false;
    },
    updateList() {
      http.get('/notice').then(({ data }) => {
        this.items = data;
        this.original = data;
        this.viewList = true;
        this.viewDetail = false;
        this.viewUpdate = false;
        this.viewRegist = false;
      });
    },
    regist() {
      this.viewList = false;
      this.viewDetail = false;
      this.viewRegist = true;
    },
    updateForm() {
      this.viewList = false;
      this.viewDetail = false;
      this.viewUpdate = true;
    },
  },
};
</script>
<style lang="css" scoped>
.black-button {
  font-family: 'Nanum Gothic', sans-serif;
  border: 2px solid #cda45e;
  border-radius: 10px;
  height: 40px;
  color: #fff;
  padding: 5px 18px;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 0.3s;
  background-color: #0c0b09;
  margin-right: 15px;
}
</style>
