                </div>
                <div class="text-center">
                  <button type="submit">보내기</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
      <!-- End Contact Section -->
    </main>
    <!-- End #main -->
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
import NewsForm from '@/components/News.vue';
const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  name: 'Home',
  components: {
    NewsForm,
  },
  data() {
    return {
      user: {
        id: '',
        password: '',
        name: '',
        email: '',
        phone: '',
        address: '',
      },
    };
  },
  methods: {
    register() {
      axios
        .post(`${SERVER_URL}/user/register`, this.user)
        .then(({ data }) => {
          console.log(data);
          if (data === 'success') {
            this.$router.replace('/login');
            //스테이트 값 바꿔주기.
            this.$store.state.userName = this.user.name;
          }
          Swal.fire({
            position: 'top',
            background: '#F6F6F6',
            icon: 'success',
            iconColor: '#F2CB61',
            text: 'Pent House에 회원이 되신걸 축하합니다.',
            showConfirmButton: false,
            timer: 2000,
          });
        })
        .catch(() => {
          Swal.fire({
            icon: 'warning',
            text: '동일한 회원의 ID가 이미 존재합니다.',
            showClass: {
              popup: 'animate__animated animate__fadeInDown',
            },
            hideClass: {
              popup: 'animate__animated animate__fadeOutUp',
            },
            background: '#F6F6F6',
            confirmButtonText: '닫기',
            confirmButtonColor: '#FFBB00',
          });
        });
    },
  },
};
</script>
