import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import Swal from 'sweetalert2';

Vue.use(Vuex);

const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default new Vuex.Store({
  state: {
    accessToken: null,
    userId: '',
    userName: '',
  },
  getters: {
    getAccessToken(state) {
      console.log(state.accessToken);
      return state.accessToken;
    },
    getUserId(state) {
      return state.userId;
    },
    getUserName(state) {
      return state.userName;
    },
  },
  mutations: {
    LOGIN(state, payload) {
      state.accessToken = payload['auth-token'];
      state.userId = payload['user-id'];
      state.userName = payload['user-name'];
      sessionStorage.setItem('loginInfo', JSON.stringify(payload));
    },
    LOGOUT(state) {
      state.accessToken = null;
      state.userId = '';
      state.userName = '';
      sessionStorage.removeItem('loginInfo');
    },
  },
  actions: {
    //추가. 스토리지에 저장해라
    LOGIN(context, user) {
      console.log('액션에서 찍는 유저' + user.id);
      return axios
        .post(`${SERVER_URL}/user/confirm/login`, user)
        .then((response) => {
          console.log(response.data.message);
          if (response.data.message === 'fail') {
            Swal.fire({
              icon: 'warning',
              position: 'top',
              text: '아이디와 비밀번호를 확인하세요.',
              showClass: {
                popup: 'animate__animated animate__fadeInDown',
              },
              hideClass: {
                popup: 'animate__animated animate__fadeOutUp',
              },
              background: '#F6F6F6',
              confirmButtonText: '닫기',
              confirmButtonColor: '#FFBB00',
            });
            return 'fail';
          } else {
            context.commit('LOGIN', response.data);
            axios.defaults.headers.common[ //엑시오스 호출시 헤더에 담아라.
              'auth-token' //이 이름으로
            ] = `${response.data['auth-token']}`; //이 토큰 정보가.
            Swal.fire({
              position: 'top',
              background: '#F6F6F6',
              icon: 'success',
              iconColor: '#F2CB61',
              text: `${user.id} 님 환영합니다.`,
              showConfirmButton: false,
              timer: 2000,
            });
          }

          //json데이터 담아.
          //console.log(sessionStorage.getItem('loginInfo'));
        });
    },
    LOGOUT(context) {
      //스토리지 지워라.
      context.commit('LOGOUT');
      axios.defaults.headers.common['auth-token'] = undefined;
    },
  },
  modules: {},
});
