<template>
  <div class="text-center form-group" style="margin-top:50px;">
    <table class="table table-bordered">
      <tr>
        <th>번호</th>
        <td>{{ item.no }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ item.writer }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(item.regtime) }}</td>
      </tr>
      <tr>
        <td colspan="2" style="height:200px">
          {{ item.content }}
        </td>
      </tr>
    </table>

    <br />
    <div class="text-center">
      <button class="black-button" @click="list">
        목록</button
      >&nbsp;&nbsp;
      <button class="black-button" @click="update">
        수정</button
      >&nbsp;&nbsp;
      <button class="black-button" @click="remove">
        삭제
      </button>
    </div>
  </div>
</template>
<script>
import Swal from 'sweetalert2';
import http from '@/util/http-common';
import moment from 'moment';
export default {
  name: 'ReviewDetail',
  props: {
    no: {
      type: Number,
      required: true,
    },
    item: {
      type: Object,
      required: true,
    },
  },
  watch: {
    no() {
      http.get(`/review/${this.no}`).then(({ data }) => {
        this.item = data;
        console.dir(data);
      });
    },
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
    },
    list() {
      this.$emit('show-list');
    },
    remove() {
      if (this.$store.state.userId != this.item.writer) {
        Swal.fire({
          icon: 'error',
          title: '삭제 실패',
          text: '본인의 게시물만 삭제할 수 있습니다.',
        });
        return;
      }
      http.delete(`/review/${this.no}`).then(() => {
        this.$emit('update-list');
      });
    },
    update() {
      if (this.$store.state.userId != this.item.writer) {
        Swal.fire({
          icon: 'error',
          title: '수정 불가',
          text: '본인의 게시물만 수정할 수 있습니다.',
        });
        return;
      }
      this.$emit('update-form');
    },
  },
};
</script>
<style lang="css" scoped>
table {
  border-color: white;
}
th {
  color: white;
}
td {
  color: white;
  text-align: left;
}
.black-button {
  font-family: 'Nanum Gothic', sans-serif;
  border: 2px solid #cda45e;
  border-radius: 20px;
  height: 40px;
  width: 100px;
  color: #fff;
  padding: 5px 18px;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 0.3s;
  background-color: #0c0b09;
  margin-right: 15px;
}
</style>
