<template>
  <div>
    <div class="row" data-aos="fade-up" data-aos-delay="100">
      <div class="col-lg-3">
        <ul class="nav nav-tabs flex-column">
          <li
            class="nav-item"
            v-for="(item, index) in paginatedData"
            :key="index"
            id="aptName"
          >
            <a
              v-if="index == 0"
              class="nav-link active show"
              data-toggle="tab"
              href="#tab-1"
              >{{ item.aptName }}</a
            >
            <a
              v-else
              class="nav-link"
              data-toggle="tab"
              :href="`#tab-${index + 1}`"
              >{{ item.aptName }}</a
            >
          </li>
        </ul>
      </div>
      <div class="col-lg-9 mt-4 mt-lg-0">
        <div id="aptDetail" class="tab-content">
          <div
            v-for="(item, index) in paginatedData"
            :key="index"
            :class="className(index)"
            :id="`tab-${index + 1}`"
          >
            <div class="row">
              <div class="col-lg-8 details order-2 order-lg-1">
                <h3>{{ item.aptName }}</h3>
                <table>
                  <colgroup>
                    <col :style="{ width: '20%' }" />
                    <col :style="{ width: '80%' }" />
                  </colgroup>
                  <tr>
                    <td>법정동명</td>
                    <td>{{ item.dong }}</td>
                  </tr>
                  <tr>
                    <td>분류</td>
                    <td>{{ item.type }}</td>
                  </tr>
                  <tr>
                    <td>거래금액</td>
                    <td v-if="item.dealAmount != null">
                      {{ item.dealAmount }}
                    </td>
                    <td v-else-if="item.rentMoney == 0">
                      {{ item.deposit }}
                    </td>
                    <td v-else>
                      보증금액 {{ item.deposit }} / 월세금액
                      {{ item.rentMoney }}
                    </td>
                  </tr>
                  <tr>
                    <td>건축 년도</td>
                    <td>{{ item.buildYear }}</td>
                  </tr>
                  <tr>
                    <td>면적</td>
                    <td>{{ item.area }}</td>
                  </tr>
                  <tr>
                    <td>층</td>
                    <td>{{ item.floor }}</td>
                  </tr>
                  <tr>
                    <td>거래 날짜</td>
                    <td>
                      {{ item.dealYear }}/{{ item.dealMonth }}/{{
                        item.dealDay
                      }}
                    </td>
                  </tr>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="pageNav">
      <button :disabled="pageNum === 0" @click="prevPage">
        이전</button
      >&nbsp; <span>{{ pageNum + 1 }} / {{ pageCount }} 페이지</span>&nbsp;
      <button :disabled="pageNum >= pageCount - 1" @click="nextPage">
        다음
      </button>
    </div>
  </div>
</template>
<script>
export default {
  name: 'DealList',
  data() {
    return {
      pageNum: 0,
    };
  },
  props: {
    listArray: {
      type: Array,
      required: true,
    },
    pageSize: {
      type: Number,
      required: false,
      default: 5,
    },
    pageZero: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  watch: {
    pageZero() {
      if (this.pageZero === true) {
        this.pageNum = 0;
        this.$emit('page-initialized');
      }
    },
  },
  methods: {
    nextPage() {
      this.pageNum++;
      this.$emit('next-page');
    },
    prevPage() {
      this.pageNum--;
      this.$emit('prev-page');
    },
    className(index) {
      if (index == 0) return 'tab-pane active show';
      else return 'tab-pane';
    },
  },
  computed: {
    pageCount() {
      let listLeng = this.listArray.length,
        listSize = this.pageSize,
        page = Math.floor(listLeng / listSize);
      if (listLeng % listSize > 0) page += 1;
      if (page == 0) page = 1;
      return page;
    },
    paginatedData() {
      const start = this.pageNum * this.pageSize,
        end = start + this.pageSize;
      return this.listArray.slice(start, end);
    },
  },
};
</script>
<style lang="css" scoped>
.pageNav {
  color: white;
  text-align: center;
  margin-top: 30px;
}
table {
  width: 100%;
}
td {
  padding: 6px;
}
.pageNav > button {
  font-family: 'Nanum Gothic', sans-serif;
  color: white;
  border-radius: 50px;
  padding: 2.8px 14px;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 0.3s;
  background-color: #cda45e;
}
.pageNav > button:disabled {
  background-color: gray;
}
</style>
