<template>
  <div>
    <!-- ======= 회원 정보 확인 ======= -->
    <section id="book-a-table" class="book-a-table">
      <div class="container" data-aos="fade-up" style="margin-top: 150px;">
        <div class="section-title">
          <h2>Membership</h2>
          <p>회 원 정 보 수 정</p>
        </div>

        <form
          @submit.prevent="doModify"
          id="mform"
          role="form"
          class="php-email-form"
          data-aos="fade-up"
          data-aos-delay="100"
        >
          <div class="form-row">
            <div class="col-lg-4 col-md-6 form-group">
              <input
                type="text"
                name="name"
                class="form-control"
                v-model="user.name"
                required
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-4 col-md-6 form-group">
              <input
                type="email"
                class="form-control"
                v-model="user.email"
                required
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-4 col-md-6 form-group">
              <input
                type="text"
                class="form-control"
                v-model="user.phone"
                required
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-6 col-md-6 form-group">
              <input
                type="text"
                class="form-control"
                readonly="readonly"
                v-model="user.id"
                style="color: #F2CB61"
              />
              <div class="validate"></div>
            </div>
            <div class="col-lg-6 col-md-6 form-group">
              <input
                type="password"
                class="form-control"
                v-model="user.password"
                style="color: #F2CB61"
                required
              />
              <div class="validate"></div>
            </div>
          </div>
          <div class="form-group">
            <textarea
              class="form-control"
              name="message"
              rows="2"
              v-model="user.address"
              required
            ></textarea>
            <div class="validate"></div>
          </div>
          <div class="text-center">
            <button type="submit">수 정 완 료</button>
          </div>
        </form>
      </div>
    </section>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';

const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  data() {
    return {
      user: {
        id: '',
        password: '',
        name: '',
        email: '',
        address: '',
        phone: '',
      },
    };
  },

  created() {
    //회원정보 바로 가져오기.
    axios
      .get(`${SERVER_URL}/user/info`)
      .then((response) => {
        // console.log(response.data.user);
        this.user = response.data.user;
        console.log('this user찍어보기' + this.user);
      })
      .catch(() => {
        this.$store.dispatch('LOGOUT').then(() => this.$router.replace('/'));
      });
  },
  methods: {
    doModify() {
      axios
        .put(`${SERVER_URL}/user/infoUpdate`, this.user)
        .then(({ data }) => {
          console.log(data);
          if (data === 'success') {
            this.$router.push('/me');
            //스테이트 값 바꿔주기.
            this.$store.state.userName = this.user.name;
            Swal.fire({
              position: 'top',
              background: '#F6F6F6',
              icon: 'success',
              iconColor: '#F2CB61',
              text: `회원 정보가 수정되었습니다.`,
              showConfirmButton: false,
              timer: 2000,
            });
          }
        })
        .catch(() => {
          alert('수정 처리시 에러가 발생했습니다.');
        });
    },
  },
};
</script>
